import React from "react";

function ONamaScreen() {
  return (
    <div>
      <h3>O nama</h3>
      <div>
      Rasadnik Ema je registrovano poljoprivredno gazdinstvo koje se bavi proizvodnjom i prodajom ukrasnih batenskih sadnica. U ponudi imamo sadnice perena, 
      ukrasnog bunja, trava, seduma, uvarkua, bobiastog voa, etinara i liara. Verujemo da ete u naoj ponudi nai biljke koje e se savreno uklopiti u Va vrt.<br></br><br></br> 

      Dobro doli! 
      </div>
    </div>
  );
}

export default ONamaScreen;
